.App {
    font-family: sans-serif;
    text-align: center;
    /* height: 99vh;
    width: 99vw; */
    margin: 0;
    padding: 20px 20px;
    overflow: hidden;
  }
  
  .container {
    position: relative;
  }
  
  .container,
  #interactive.viewport {
    width: 450px;
    height: 450px;
  }
  
  #interactive.viewport canvas,
  video {
    width: 450px;
    height: 450px;
    position: absolute;
    top: 0;
    left: 0;
  }
  
  #interactive.viewport canvas.drawingBuffer,
  video.drawingBuffer {
    width: 450px;
    height: 450px;
  }
  